import React from "react"
import * as styles from "./tableOfContents.module.scss"

export default function TableOfContents(props) {
  const items = props.items
  if (!items) {
    return ""
  }
  return (
    <div className={styles.tocRoot}>
      <h3 style={{ textAlign: "center" }}>Contents</h3>
      <Toc items={items} />
    </div>
  )
}

function Toc(props) {
  const items = props.items
  return (
    <ul>
      {items.map(item => (
        <li>
          {item.title}
          {item.items && <Toc items={item.items} />}
        </li>
      ))}
    </ul>
  )
}
